<template>
    <ManageNewsCreate></ManageNewsCreate>
    <b-tab title="消息管理" active>
        <b-row>
            <b-col md="4">
                <b-list-group v-for="(item,index) in news" :key="index">
                    <b-list-group-item active v-if="index == editing.current" @click="select(index)">
                        <b-badge>{{ item.id }}</b-badge>
                        {{ item.title }}
                    </b-list-group-item>
                    <b-list-group-item v-if="index != editing.current" @click="select(index)">
                        <b-badge>{{ item.id }}</b-badge>
                        {{ item.title }}
                    </b-list-group-item>
                </b-list-group>
                <div class="d-flex justify-content-center my-2">
                    <nav aria-label="Page navigation example">
                        <ul class="pagination justify-content-end">
                            <li class="page-item disabled" v-if="page.current == 1">
                                <a @click="prevPage" class="page-link">Previous</a>
                            </li>
                            <li class="page-item" v-if="page.current != 1">
                                <a @click="prevPage" class="page-link">Previous</a>
                            </li>
                            <li class="page-item">
                                <a class="page-link bg-primary text-light fw-bolder">{{ page.current }}</a>
                            </li>
                            <li class="page-item disabled" v-if="page.current == page.all">
                                <a @click="nextPage" class="page-link">Next</a>
                            </li>
                            <li class="page-item" v-if="page.current != page.all">
                                <a @click="nextPage" class="page-link">Next</a>
                            </li>
                        </ul>
                    </nav>
                </div>
            </b-col>
            <b-col md="8">
                <b-alert v-model="response.status" variant="success" dismissible>
                    操作成功
                </b-alert>
                <b-alert v-model="response.error" variant="danger" dismissible>
                    {{ response.feedback }}
                </b-alert>
            
                <b-row class="my-2">
                    <b-col md="2">
                        <label for="">標題編號</label>
                    </b-col>
                    <b-col md="10">
                        <b-form-input v-model="editing.data.id" placeholder="Enter your name" disabled></b-form-input>
                    </b-col>
                </b-row>
                <b-row class="my-2">
                    <b-col md="2">
                        <label for="">消息標題</label>
                    </b-col>
                    <b-col md="10">
                        <b-form-input v-model="editing.data.title" placeholder="Enter your name"></b-form-input>
                    </b-col>
                </b-row>
                <b-row class="my-2">
                    <b-col md="2">
                        <label for="">內文</label>
                    </b-col>
                    <b-col md="10">
                        <ckeditor v-model="editing.data.context" :config="editorConfig" :editor="editor"></ckeditor>
                    </b-col>
                </b-row>
                <b-row class="my-2">
                    <b-col md="2">
                        <label for="">消息類型</label>
                    </b-col>
                    <b-col md="10">
                        <b-form-select v-model="editing.notification.id" :options="options"></b-form-select>
                    </b-col>
                </b-row>
                <b-row class="my-2">
                    <b-col md="2">
                        <label for="">編輯者</label>
                    </b-col>
                    <b-col md="10">
                        <b-form-input disabled v-model="editing.data.editor" placeholder="Enter your name"></b-form-input>
                    </b-col>
                </b-row>
                <b-row class="my-2">
                    <b-col md="2">
                        <label for="">建立時間</label>
                    </b-col>
                    <b-col md="10">
                        {{ moment(editing.data.created_at).format("YYYY-MM-DD HH:MM") }}
                    </b-col>
                </b-row>
                <b-row class="my-2">
                    <b-col md="2">
                        <label for="">編輯時間</label>
                    </b-col>
                    <b-col md="10">
                        {{ moment(editing.data.updated_at).format("YYYY-MM-DD HH:MM") }}
                    </b-col>
                </b-row>
                <b-row class="my-2">
                    <b-col md="2">
                        <label for="">下架時間</label>
                    </b-col>
                    <b-col md="10">
                        {{ moment(editing.data.deleted_at).format("YYYY-MM-DD HH:MM") }}
                    </b-col>
                </b-row>
                <b-row>
                    <b-col md="12 text-center">
                        <b-button variant="success m-2" @click="updateData(editing.data.id)">更新</b-button>
                        <b-button variant="primary m-2" @click="topData(editing.data.id)">
                            <span v-if="!editing.data.top">置頂</span>
                            <span v-if="editing.data.top">取消置頂</span>
                        </b-button>
                        <b-button variant="warning m-2" @click="softDelete(editing.data.id)">
                            <span v-if="editing.data.deleted_at == null">下架</span>
                            <span v-if="editing.data.deleted_at != null">取消下架</span>
                        </b-button>
                        <b-button variant="danger m-2" @click="deleteData(editing.data.id)">刪除</b-button>
                    </b-col>
                </b-row>
            </b-col>
        </b-row>
    </b-tab>
</template>

<script>
    import moment from 'moment';
    import ManageNewsCreate from './Manage-News-Create.vue';
    import { NewsServer } from "../../providers/http-server.js";
    import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
    import MyUploadAdapter from '../../libs/myUploadAdapter';

    function MyCustomUploadAdapterPlugin(editor) {
        editor.plugins.get('FileRepository').createUploadAdapter = (loader) => new MyUploadAdapter(loader);
    }

    export default {
        name: 'App',
        components: {
            ManageNewsCreate
        },
        data(){
            return {
                options: [
                    { value: 1, text: '公告' },
                    { value: 2, text: '研習' },
                    { value: 3, text: '活動' },
                    { value: 4, text: '競賽' },
                    { value: 5, text: '徵才' }
                ],
                news: [],
                editing: {
                    current: 0,
                    data: [],
                    notification: []
                },
                response: {
                    status: false,
                    error: false,
                    feedback: "",
                },
                page: {
                    current: 1,
                    all: 1
                },
                editor: ClassicEditor,
                editorConfig: {
                    extraPlugins: [MyCustomUploadAdapterPlugin],
                }
            }
        },
        created(){
            this.moment = moment;
            NewsServer.readALL().then(
                (response) => {
                    this.news = response.data.result;
                    this.editing.data = this.news[0];
                    this.editing.notification = this.news[0].notification;
                    this.page.current = response.data.pages.current;
                    this.page.all = response.data.pages.all;
                }
            );
            
        },
        methods: {
            updateData(id){
                let data = {
                    "title": this.editing.data.title,
                    "context": this.editing.data.context,
                    "type": Number(this.editing.notification.id),
                };
                NewsServer.update(id, data).then(() => {
                    this.response.error = false;
                    this.response.status = true;
                }).catch(()=> {
                    this.response.error = true;
                    this.response.status = true;
                })
            },
            softDelete(id){
                NewsServer.softdelete(id, null).then(() => {
                    this.response.error = false;
                    this.response.status = true;
                }).catch(()=> {
                    this.response.error = true;
                    this.response.status = true;
                })
            },
            deleteData(id){
                NewsServer.delete(id, null).then(() => {
                    this.response.error = false;
                    this.response.status = true;
                }).catch(()=> {
                    this.response.error = true;
                    this.response.status = true;
                });
            },
            topData(id){
                NewsServer.top(id, null).then(() => {
                    this.response.error = false;
                    this.response.status = true;
                }).catch(()=> {
                    this.response.error = true;
                    this.response.status = true;
                });
            },
            select(num){
                this.response.status = false;
                this.editing.current = num;
                this.editing.data = this.news[num];
                this.editing.notification = this.news[num].notification;
            },
            prevPage() {
                this.page.current -= 1;
                NewsServer.readALL({
                    page: this.page.current
                }).then(
                    (response) => {
                        this.news = response.data.result;
                        this.editing.data = this.news[0];
                        this.editing.notification = this.news[0].notification;
                        this.page.current = response.data.pages.current;
                        this.page.all = response.data.pages.all;
                    }
                );
            },
            nextPage() {
                this.page.current += 1;
                NewsServer.readALL({
                    page: this.page.current
                }).then(
                    (response) => {
                        this.news = response.data.result;
                        this.editing.data = this.news[0];
                        this.editing.notification = this.news[0].notification;
                        this.page.current = response.data.pages.current;
                        this.page.all = response.data.pages.all;
                    }
                );
            },
            locationPage(num) {
                this.current = num;
                NewsServer.readALL({
                    page: num
                }).then(
                    (response) => {
                        this.news = response.data.result;
                        this.editing.data = this.news[0];
                        this.editing.notification = this.news[0].notification;
                        this.page.current = response.data.pages.current;
                        this.page.all = response.data.pages.all;
                    }
                );
            }
        }
    }
</script>

<style scoped></style>