<template>
    <div class="container">
        <div v-if="!response.status">
            <h2 class="text-center my-4">管理者登入</h2>
            <b-alert v-model="response.error" variant="danger" dismissible>
                {{ response.feedback }}
            </b-alert>
            <b-row class="my-4">
                <b-col sm="3">
                    <label>電子郵件</label>
                </b-col>
                <b-col sm="9">
                    <b-form-input v-model="data.email" type="email" placeholder="Enter your Email"></b-form-input>
                </b-col>
            </b-row>
            <b-row class="my-4">
                <b-col sm="3">
                    <label>密碼</label>
                </b-col>
                <b-col sm="9">
                    <b-form-input v-model="data.password" type="password" placeholder="Enter your Password"></b-form-input>
                </b-col>
            </b-row>
            <div class="my-4 text-center">
                <b-button @click="login">登入</b-button>
            </div>
        </div>
        <!--  -->
        <div v-if="response.status && !response.error">
            <h2>管理者頁面</h2>
            <div class="text-end">
                歡迎回來! {{ user.name }} 使用者 <b-button @click="logout">登出</b-button>
            </div>
            <b-tabs content-class="mt-3" fill>
                <b-tab id="news" title="消息管理" active>
                    <Manage-News></Manage-News>
                </b-tab>
                <b-tab id="banner" title="看板管理">
                    <Manage-Banner></Manage-Banner>
                </b-tab>
                <b-tab id="honor" title="榮譽榜管理">
                    <ManageHonor></ManageHonor>
                </b-tab>
                <b-tab id="album" title="相簿管理">
                    <Manage-Album></Manage-Album>
                </b-tab>
                <b-tab id="link" title="連結管理">
                    <Manage-Link></Manage-Link>
                </b-tab>
                <b-tab id="account" title="成員列表">
                    <Manage-User></Manage-User>
                </b-tab>
            </b-tabs>
        </div>
    </div>
</template>

<script>
import ManageNews from '../components/Manage-News.vue';
import ManageBanner from '../components/Manage-Banner.vue';
import ManageLink from '../components/Manage-Link.vue';
import ManageHonor from '../components/Manage-Honor.vue';
import ManageUser from '../components/Manage-User.vue';
import ManageAlbum from '../components/Manage-Album.vue';
import { UserServer } from '../../providers/http-server.js';

export default {
    components: { 
        ManageNews,
        ManageBanner,
        ManageLink,
        ManageHonor,
        ManageUser,
        ManageAlbum
    },
    name: 'App',
    data(){
        return {
            data: {
                email: '',
                password: ''
            },
            response: {
                status: false,
                feedback: false,
                error: false,
                code: 200
            },
            user: {
                id: 0,
                name: ''
            }
        }
    },
    created(){
        if(localStorage.getItem('API_TOKEN') != null) {
            this.response.status = true;
            this.user.name = localStorage.getItem('USER_NAME');
            this.user.id = localStorage.getItem('USER_ID');
        }
    },
    methods:  {
        login() {
            UserServer.Login(this.data).then(
                (response) => {
                    this.response.status = response.status;
                    if(this.response.status){
                        localStorage.setItem('API_TOKEN', response.parameter);
                        localStorage.setItem('USER_NAME', response.data.name);
                        localStorage.setItem('USER_ID', response.data.id);
                    }
                }
            );
        },
        logout() {
            UserServer.Logout().then(() => {
                localStorage.removeItem("API_TOKEN");
                localStorage.removeItem("USER_NAME");
                localStorage.removeItem("USER_ID");
                this.$router.go(0);
            }).catch(
                // (error) => console.log(error)
            );
        }
    }
}
</script>

<style scoped></style>