<template>
    <ManageBannerCreate></ManageBannerCreate>

    <b-modal
        id="modal-prevent-closing-2"
        ref="modal-2"
        title="修改看板資料"
        @ok="handleOk"
    >
        <form ref="form" @submit.stop.prevent="handleSubmit">
            <b-form-group
                label="看版標題"
                label-for="UPDATE_BANNER_TITLE"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <b-form-input
                    id="UPDATE_BANNER_TITLE"
                    v-model="editBanner.data.title"
                    :state="nameState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
                label="看版說明"
                label-for="UPDATE_BANNER_DESCRIPTION"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <ckeditor v-model="editBanner.data.description" :config="editorConfig" :editor="editor"></ckeditor>
            </b-form-group>
        </form>
    </b-modal>

    <div v-for="(items, index) in banners" :key="index">
        <b-card class="overflow-hidden my-2">
            <b-row no-gutters>
                <b-col md="6">
                    <b-card-img 
                        :src="'http://localhost/ntcust_lang_backend/storage/app/' + items.file.path"
                        class="rounded-0">
                    </b-card-img>
                </b-col>
                <b-col md="6">
                    <b-card-body :title="items.title">
                        <b-card-text>
                            <table class="table">
                                <tbody>
                                    <tr>
                                        <td>編號</td>
                                        <td>{{ items.id }}</td>
                                    </tr>
                                    <tr>
                                        <td>說明</td>
                                        <td v-html="items.description"></td>
                                    </tr>
                                    <tr>
                                        <td>新增時間</td>
                                        <td>{{ moment(items.created_at).format("YYYY-MM-DD HH:MM") }}</td>
                                    </tr>
                                    <tr>
                                        <td>修改時間</td>
                                        <td>{{ moment(items.updated_at).format("YYYY-MM-DD HH:MM") }}</td>
                                    </tr>
                                </tbody>
                            </table>
                            <b-row>
                                <b-col md="12" class="text-end">
                                    <b-button 
                                    variant="success m-1 text-end"
                                    @click="edit(index)" v-b-modal.modal-prevent-closing-2>
                                        修改
                                    </b-button>
                                    <b-button @click="remove(items.id)" variant="danger m-1">刪除</b-button>
                                </b-col>
                            </b-row>
                        </b-card-text>
                    </b-card-body>
                </b-col>
            </b-row>
        </b-card>
    </div>
</template>

<script>
import ManageBannerCreate from './Manage-Banner-Create.vue';
import moment from 'moment';
import { BannerServer } from "../../providers/http-server.js";
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

let editor = ClassicEditor;
let editorConfig = {
    toolbar: {
    }
};

export default {
    name: 'App',
    components: {
        ManageBannerCreate
    },
    data(){
        return {
            // Modal
            nameState: null,
            submittedNames: [],
            // 渲染資料
            banners: null,
            editBanner: {
                data: [],
                index: 0
            },
            editor,
            editorConfig
        }
    },
    created(){
        this.moment = moment;
        BannerServer.read().then(
            (response) => {
                this.banners = response.data;
            }
        );
    },
    methods: 
    {
        edit(index)
        {
            this.editBanner.index = index;
            this.editBanner.data = this.banners[this.editBanner.index];
        },
        remove(id)
        {
            BannerServer.delete(id).then(() => {
                // console.log(response);
            });
        },
        checkFormValidity() {
            const valid = this.$refs.form.checkValidity()
            this.nameState = valid
            return valid
        },
        handleOk() {
            this.handleSubmit()
        },
        handleSubmit() {
            // Exit when the form isn't valid
            if (!this.checkFormValidity()) {
                return
            }
            // Push the filed to submitted axios
            BannerServer.update(this.editBanner.data.id, this.editBanner.data).then(() => {
                // console.log();
            });
            // Hide the modal manually
            this.$nextTick(() => {
                this.$bvModal.hide('modal-prevent-closing-2')
            })
        }
    }
}
</script>

<style scoped></style>