<template>
    <ManageHonorCreate></ManageHonorCreate>

    <!-- 浮動式修改面板 -->
    <b-modal
        id="modal-prevent-closing"
        ref="modal"
        title="修改榮譽榜資料"
        @ok="handleOk"
    >
        <form ref="form" @submit.stop.prevent="handleSubmit">
            <b-form-group
                label="榮譽榜標題"
                label-for="UPDATE_HONOR_TITLE"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <b-form-input
                    id="UPDATE_HONOR_TITLE"
                    v-model="editHonor.data.title"
                    :state="nameState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
                label="榮譽榜說明"
                label-for="UPDATE_HONOR_DESCRIPTION"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <ckeditor v-model="editHonor.data.content" :config="editorConfig" :editor="editor"></ckeditor>
            </b-form-group>
            
            <b-form-group
                label="排名"
                label-for="UPDATE_HONOR_RANKING"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <b-form-input 
                    id="UPDATE_HONOR_RANKING"
                    v-model="editHonor.data.ranking" 
                    :state="nameState"
                    required
                    placeholder="請填入得獎名次">
                </b-form-input>
                <b-form-select 
                    v-model="editHonor.data.ranking_style" 
                    :options="options.ranking_styles">
                </b-form-select>
            </b-form-group>
            <b-form-group
                label="科系"
                label-for="UPDATE_HONOR_DEPARTMENT"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <b-form-select 
                    v-model="editHonor.data.department" 
                    :options="options.departments">
                </b-form-select>
            </b-form-group>
        </form>
    </b-modal>

    <div v-for="(item, index) in honor" :key="index">
        <b-card border-variant="info mt-3" align="left">
            <b-card-title>
                <span v-if="item.ranking_style == 1" class="badge rounded-pill bg-warning text-dark">{{ item.ranking }}</span>
                <span v-if="item.ranking_style == 2" class="badge rounded-pill bg-light text-dark">{{ item.ranking }}</span>
                <span v-if="item.ranking_style == 3" class="badge rounded-pill bg-secondary">{{ item.ranking }}</span>
                <span v-if="item.ranking_style == 0" class="badge rounded-pill bg-primary">{{ item.ranking }}</span>
                <!--  -->
                <span class="mx-3">{{ item.title }}</span>
                <!--  -->
                <b-badge v-if="item.department == 41" pill variant="dark">應用英文系</b-badge>
                <b-badge v-if="item.department == 42" pill variant="dark">應用日文系</b-badge>
                <b-badge v-if="item.department == 43" pill variant="dark">應用中文系</b-badge>
            </b-card-title>
            <b-card-sub-title class="mb-2">
                建立時間 {{ moment(item.created_at).format("YYYY-MM-DD HH:MM:SS") }}
                |
                更新時間 {{ moment(item.updated_at).format("YYYY-MM-DD HH:MM:SS") }}
            </b-card-sub-title>
            <b-card-text>
                <b-alert show variant="light" v-html="item.content"></b-alert>
                <b-row>
                    <b-col md="12 text-end">
                        <b-button 
                        variant="success m-1 text-end"
                        @click="edit(index)" v-b-modal.modal-prevent-closing>
                            修改
                        </b-button>
                        <b-button @click="remove(item.id)" variant="danger m-1">
                            刪除
                        </b-button>
                    </b-col>
                </b-row>
            </b-card-text>
        </b-card>
    </div>
</template>

<script>
import moment from 'moment';
import ManageHonorCreate from '../components/Manage-Honor-Create.vue';
import { HonorServer } from '../../providers/http-server';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import MyUploadAdapter from '../../libs/myUploadAdapter';

function MyCustomUploadAdapterPlugin(editor) {
    editor.plugins.get('FileRepository').createUploadAdapter = (loader) => new MyUploadAdapter(loader);
}

export default {
    name: 'App',
    components: {
        ManageHonorCreate
    },
    data(){
        return {
            honor: [],
            // Modal
            nameState: null,
            submittedNames: [],
            modalShow: false,
            
            editHonor: {
                index: 0,
                data: []
            },
            options: {
                departments: [
                    { value: 41, text: '應用英文系' },
                    { value: 42, text: '應用日文系' },
                    { value: 43, text: '應用中文系' },
                ],
                ranking_styles: [
                    { value: 0, text: '預設樣式' },
                    { value: 1, text: '第一名(金牌)' },
                    { value: 2, text: '第二名(銀牌)' },
                    { value: 3, text: '第三名(銅牌)' },
                ]
            },
            editor: ClassicEditor,
            editorConfig: {
                toolbar: {
                },
                extraPlugins: [MyCustomUploadAdapterPlugin],
            }
        }
    },
    created(){
        this.moment = moment;
        HonorServer.read().then(
            (response) => this.honor = response.data
        );
    },
    methods: 
    {
        edit(index)
        {
            this.editHonor.index = index;
            this.editHonor.data = this.honor[this.editHonor.index];
        },
        checkFormValidity() {
            const valid = this.$refs.form.checkValidity()
            this.nameState = valid
            return valid
        },
        handleOk() {
            this.handleSubmit()
        },
        handleSubmit() {
            // Exit when the form isn't valid
            if (!this.checkFormValidity()) {
                return
            }
            // Push the filed to submitted axios
            HonorServer.update(this.editHonor.data.id, this.editHonor.data).then(() => {
                // console.log(response)
            });
            
            // Hide the modal manually
            this.$nextTick(() => {
                this.$bvModal.hide('modal-prevent-closing')
            })
        },
        remove(id) {
            HonorServer.delete(id).then((response) => {
                console.log(response)
            });
        }
    }
}
</script>

<style scoped></style>