<template>
    <!-- 浮動式修改面板 -->
    <b-modal
        id="modal-prevent-closing-5"
        ref="modal-5"
        title="建立使用者"
        @ok="createUserData"
    >
        <form ref="form" @submit.stop.prevent="handleSubmit">
            <b-form-group
                label="使用者名稱"
                label-for="CREATE_USER_NAME"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <b-form-input
                    id="CREATE_USER_NAME"
                    v-model="createUser.name"
                    :state="nameState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
                label="電子郵件"
                label-for="CREATE_USER_EMAIL"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <b-form-input
                    id="CREATE_USER_EMAIL"
                    v-model="createUser.email"
                    :state="nameState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
                label="密碼"
                label-for="CREATE_USER_PASSWORD"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <b-form-input
                    id="CREATE_USER_PASSWORD"
                    v-model="createUser.passsword"
                    :state="nameState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
                label="權限"
                label-for="CREATE_USER_LEVEL"
                invalid-feedback="Name is required"
                :state="nameState"
            >
                <b-form-input
                    id="CREATE_USER_LEVEL"
                    v-model="createUser.level"
                    :state="nameState"
                    required
                ></b-form-input>
            </b-form-group>
        </form>
    </b-modal>
    <!--  -->
    <div class="container">
        <b-button variant="success m-2" 
            v-b-popover.hover.bottom="`說明:建立使用者為可以操作後端之功能，並不會出現在學院成員之中。
            註:您需要為權限最高者，才可進行此操作。`"
            v-b-modal.modal-prevent-closing-5
            title="建立使用者">
            建立使用者
        </b-button>
        <b-row>
            <b-col md="2">
                <b-list-group v-for="(item,index) in members" :key="index">
                    <b-list-group-item active v-if="index == current" @click="select(index)">
                        <b-badge>{{ item.id }}</b-badge>
                        {{ item.name }}
                    </b-list-group-item>
                    <b-list-group-item v-if="index != current" @click="select(index)">
                        <b-badge>{{ item.id }}</b-badge>
                        {{ item.name }}
                    </b-list-group-item>
                </b-list-group>
            </b-col>
            <b-col md="10 text-center">
                <b-card
                    :title="editMember.name + ' ' + editMember.title"
                    img-top
                    class="mb-2"
                >
                    <b-card-text>
                        <b-row>
                            <b-col md="3">
                                <b-avatar src="https://placekitten.com/300/300" size="10rem"></b-avatar>
                            </b-col>
                            <b-col md="9 text-start">
                                <b-tabs content-class="mt-3" align="center">
                                    <b-tab title="使用者資料" active>
                                        <table class="table text-start">
                                            <tbody>
                                                <tr>
                                                    <td>使用者編號</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.id"></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>電子郵件</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.email"></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>密碼</td>
                                                    <td>
                                                        <b-form-input v-model="password"></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>權限等級</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.level" disabled></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>建立時間</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.created_at" disabled></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>更新時間</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.updated_at" disabled></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>停用時間</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.banned_at" disabled></b-form-input>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                        <div class="text-center">
                                            <b-button variant="warning m-2" 
                                                v-b-popover.hover.bottom="`您需要為權限最高者
                                                ，才可進行此操作。`"
                                                @click="banUserData" 
                                                title="停權使用者">
                                                停權使用者
                                            </b-button>
                                            <b-button variant="success m-2" 
                                                v-b-popover.hover.bottom="`您需要為權限最高者，
                                                或者操作的是自己，才可進行此操作。`"
                                                @click="editUserData" 
                                                title="修改使用者">
                                                修改使用者
                                            </b-button>
                                            <b-button variant="danger m-2" 
                                                v-b-popover.hover.bottom="'您需要為權限最高者，才可進行此操作。'"
                                                @click="deleteUserData" 
                                                title="刪除使用者">
                                                刪除使用者
                                            </b-button>
                                        </div>
                                    </b-tab>
                                    <b-tab title="成員資料" >
                                        <table class="table text-start">
                                            <tbody>
                                                <tr>
                                                    <td>頭銜</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.title"></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>職稱</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.position"></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>學歷</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.education"></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>研究領域</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.research"></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>TEL</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.tel"></b-form-input>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>Office</td>
                                                    <td>
                                                        <b-form-input v-model="editMember.office"></b-form-input>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                        <div class="text-center">
                                            <b-button variant="outline-primary m-2" 
                                                v-if="editMember.user_id == null"
                                                v-b-popover.hover.bottom="`請注意:每個欄位皆為必填，
                                                且您一旦新增資料，該使用者就會顯示在學院成員的名單之中。 
                                                註:您需要為權限最高者，才可進行此操作。`"
                                                @click="createMemberData" 
                                                title="新增成員">
                                                新增成員
                                            </b-button>
                                            <b-button variant="outline-success m-2" 
                                                v-if="editMember.user_id != null"
                                                v-b-popover.hover.bottom="`您需要為權限最高者，
                                                或者操作的是自己，才可進行此操作。`"
                                                @click="editMemberData" 
                                                title="修改成員">
                                                修改成員
                                            </b-button>
                                            <b-button variant="outline-danger m-2" 
                                                v-if="editMember.user_id != null"
                                                v-b-popover.hover.bottom="`您需要為權限最高者，
                                                或者操作的是自己，才可進行此操作。`"
                                                @click="deleteMemberData" 
                                                title="刪除成員">
                                                刪除成員
                                            </b-button>
                                        </div>
                                    </b-tab>
                                </b-tabs>
                            </b-col>
                        </b-row>
                    </b-card-text>
                </b-card>
            </b-col>
        </b-row>
    </div>
</template>

<script>
import axios from 'axios';
import moment from 'moment';
import { UserServer } from '../../providers/http-server';

export default {
    name: 'App',
    data(){
        return {
            members: [],
            editMember: [],
            createUser: {
                name: '',
                email: '',
                password: ''
            },
            password: '',
            current: 0,
            // Modal
            nameState: null,
            submittedNames: [],
            modalShow: false,
        }
    },
    created() {
        this.moment = moment;
        UserServer.ReadAccount().then(
            (response) => {
                this.members = response.data;
                this.editMember = this.members[this.current];
            }
        );
    },
    methods: {
        checkFormValidity() {
            const valid = this.$refs.form.checkValidity()
            this.nameState = valid
            return valid
        },
        createUserData() {
            UserServer.Register(this.createUser).then(
                // (response) => console.log(response.data)
            )
        },
        editUserData() {
            let data = {
                email: this.editMember.email,
                password: this.password,
                name: this.editMember.name,
                level: this.editMember.level
            };
            UserServer.UpdateAccount(this.editMember.id, data).then(
                // (response) => console.log(response)
            );
        },
        deleteUserData() {
            UserServer.DeleteAccount(this.editMember.id).then(
                // (response) => console.log(response)
            );
            this.$router.go(0);
        },
        banUserData() {
            UserServer.BanAccount(this.editMember.id).then(
                // (response) => console.log(response)
            );
            this.$router.go(0);
        },
        createMemberData() {
            let data = {
                title: this.editMember.title,
                position: this.editMember.position,
                education: this.editMember.education,
                research: this.editMember.research,
                office: this.editMember.office,
                tel: this.editMember.tel
            };
            UserServer.CreateMember(data).then(
                // (response) => console.log(response)
            );
        },
        editMemberData() {
            let data = {
                title: this.editMember.title,
                position: this.editMember.position,
                education: this.editMember.education,
                research: this.editMember.research,
                office: this.editMember.office,
                tel: this.editMember.tel
            };
            UserServer.UpdateMember(this.editMember.id, data).then(
                // (response) => console.log(response)
            );
        },
        deleteMemberData() {
            UserServer.DeleteMember(this.editMember.id).then(
                // (response) => console.log(response)
            );
            axios({
                method: 'delete',
                url: 'http://localhost/ntcust_lang_backend/public/api/auth/member/delete/' + this.editMember.id,
                headers: {
                    "Content-Type": "application/json",
                    "Accept": "application/json",
                    "Authorization": 'Bearer ' + localStorage.getItem('API_TOKEN'),
                }
            })
            .then( () => {
                // console.log(response.data);
            })
            .catch( () => {
                // console.log(Promise.reject(error));
            });
        },
        handleSubmit()
        {
            if (!this.checkFormValidity()) {
                return
            }
            // Hide the modal manually
            this.$nextTick(() => {
                this.$bvModal.hide('modal-prevent-closing')
            })
        },
        select(index){
            this.current = index;
            this.editMember = this.members[this.current];
        }
    }
}
</script>

<style scoped></style>