<template>
    <Response :error="error" :feedback="feedback"></Response>
    <b-button v-b-toggle.collapse-2 variant="success m-2">建立看版</b-button>
    <form id="CREATE_BANNER_FORM" ref="form" class="" @submit.prevent="handleSubmit">
        <div v-if="message"
        class="bg-green-600 text-white rounded p-4 mb-8">
        {{ message }}
        </div>
    <b-collapse id="collapse-2" class="m-2">
        <b-card>
            <b-row class="my-2">
                <b-col md="2">
                    <label for="">看版名稱</label>
                </b-col>
                <b-col md="10">
                    <b-form-input v-model="title" placeholder="請填入看版名稱"></b-form-input>
                </b-col>
            </b-row>
            <b-row class="my-2">
                <b-col md="2">
                    <label for="">看版說明</label>
                </b-col>
                <b-col md="10">
                    <ckeditor v-model="description" :config="editorConfig" :editor="editor"></ckeditor>
                </b-col>
            </b-row>
            <b-row class="my-2">
                <b-col md="2">
                    <label for="">上傳封面</label>
                </b-col>
                <b-col md="10">
                    <!--  -->
                    <div class="min-h-screen flex items-center justify-center">
                        <div v-if="message"
                            class="bg-green-600 text-white rounded p-4 mb-8">{{ message }}</div>
                        <img v-show="imageUrl"
                            :src="imageUrl"
                            class="w-48 h-48 object-cover"
                            width="200">
                        <div>
                            <input type="file"
                                accept="image/*"
                                @change="handleImageSelected">
                        </div>
                        <div class="flex flex-wrap mt-8">
                            <img v-for="(image, key) in images"
                                :key="key"
                                :src="image.image"
                                class="w-48 h-46 object-cover mr-4 mb-4 shadow rounded">
                        </div>
                    </div>
                    <!--  -->
                </b-col>
                <b-col md="12 text-center">
                    <b-button type="submit" variant="success m-2">創建</b-button>
                </b-col>
            </b-row>
        </b-card>
    </b-collapse>
    </form>
</template>

<script>
import { useImageUpload } from './useImageUpload';
import { ref } from "vue";
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import Response from './../components/response.vue'

let editor = ClassicEditor;
let editorConfig = {
    toolbar: {
    }
};

export default {
    name: "Home",
    setup() {
        let message = ref("");
        let images = ref([]);
        let description = ref("");
        let title = ref("");
        let error = ref("");
        let feedback = ref("");

        let { imageFile, imageUrl, handleImageSelected } = useImageUpload();

        function handleSubmit() {
            let formData = new FormData();
            formData.append("file_id", imageFile.value);
            formData.append("title", title.value);
            formData.append("description", description.value);
            var request = new XMLHttpRequest();
            request.open("POST", "http://localhost/ntcust_lang_backend/public/api/banner/create");
            request.setRequestHeader('Authorization', 'Bearer ' + localStorage.getItem('API_TOKEN'));
            request.send(formData);
            request.onreadystatechange = function () {
                if (request.readyState === 4) {
                    if (request.status === 200 && request.statusText === 'OK') {
                        this.error = false;
                        this.$router.go(0);
                    } else {
                        this.error = false;
                        this.feedback = request.response.data.feedback;
                    }
                }
            }
            imageUrl.value = "";
            imageFile.value = "";
        }
        return {
            handleImageSelected,
            imageUrl,
            handleSubmit,
            message,
            images,
            description,
            title,
            editor,
            editorConfig,
            Response,
            feedback,
            error
        };
    }
};
</script>


<style scoped></style>