<template>
    <b-button v-b-toggle.collapse-2 variant="success m-2">建立相簿</b-button>
    <b-collapse id="collapse-2" class="m-2">
        <b-card v-if="!message">
            <b-row class="my-2">
                <b-col md="2">
                    <label for="">相簿名稱</label>
                </b-col>
                <b-col md="10">
                    <b-form-input v-model="albumName" placeholder="請填入相簿名稱"></b-form-input>
                </b-col>
            </b-row>
            <b-row class="my-2">
                <b-col md="2">
                    <label for="">相簿說明</label>
                </b-col>
                <b-col md="10">
                    <ckeditor v-model="albumDescription" :config="editorConfig" :editor="editor"></ckeditor>
                </b-col>
            </b-row>
            <b-row class="my-2">
                <b-col md="2">
                    <label for="">相簿內文</label>
                </b-col>
                <b-col md="10">                      
                    <ckeditor v-model="albumContext" :config="editorConfig" :editor="editor"></ckeditor>
                </b-col>
            </b-row>
            <b-row class="my-2 text-center">
                <b-col md="12">
                    <b-button variant="success m-2" 
                        @click="createData" 
                        v-b-popover.hover.bottom="'下一步再上傳圖片'" 
                        title="創建相簿">
                        下一步
                    </b-button>
                </b-col>
            </b-row>
        </b-card>
        <!--  -->
        <form id="form" ref="form" class="" @submit.prevent="handleSubmit">
        <b-card v-if="message">
            <b-alert show variant="success">{{ message }}</b-alert>
            <b-row class="my-2">
                <b-col md="2">
                    <label for="">新增圖片</label>
                </b-col>
                <b-col md="10">
                    <div class="min-h-screen flex items-center justify-center">
                        <img v-show="imageUrl"
                            :src="imageUrl"
                            class="w-48 h-48 object-cover"
                            width="200">
                        <div>
                            <input type="file"
                                accept="image/*"
                                @change="handleImageSelected">
                        </div>
                        <div class="flex flex-wrap mt-8">
                            <img v-for="(image, key) in images"
                                :key="key"
                                :src="image.image"
                                class="w-48 h-46 object-cover mr-4 mb-4 shadow rounded">
                        </div>
                    </div>
                </b-col>
            </b-row>
            <b-row class="my-2 text-center">
                <b-col md="12">
                    <b-button variant="success m-2" 
                        type="submit"
                        v-b-popover.hover.bottom="'如果還需要上傳圖片，請先按此按鈕。'" 
                        title="上傳圖片">
                        確認圖片
                    </b-button>
                    <b-button variant="primary m-2" 
                        v-b-popover.hover.bottom="'如果無需要再上傳圖片，請按此按鈕。'"
                        @click="completeAlbum" 
                        title="完成編輯">
                        建立完成
                    </b-button>
                </b-col>
            </b-row>
        </b-card>
        </form>
        <!--  -->
    </b-collapse>
</template>

<script>
// import axios from 'axios';
import { useImageUpload } from './useImageUpload';
import { AlbumServer, FileLinkServer } from '../../providers/http-server';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { ref } from "vue";
import MyUploadAdapter from '../../libs/myUploadAdapter';

function MyCustomUploadAdapterPlugin(editor) {
    editor.plugins.get('FileRepository').createUploadAdapter = (loader) => new MyUploadAdapter(loader);
}


let editor = ClassicEditor;
let editorConfig = {
    toolbar: {
    },
    extraPlugins: [MyCustomUploadAdapterPlugin],
};

export default {
    name: "Home",
    setup() {
        var message = ref("");
        var images = ref([]);
        var albumName = ref("");
        var albumDescription = ref("");
        var albumContext = ref("");
        var successID = 0;
        var numPhoto = 0;
            
        var { imageFile, imageUrl, handleImageSelected } = useImageUpload();

        function handleSubmit() {
            var formData = new FormData();
            formData.append("file", imageFile.value);
            var request = new XMLHttpRequest();
            request.open("POST", "http://localhost/ntcust_lang_backend/public/api/file/create");
            request.setRequestHeader('Authorization', 'Bearer ' + localStorage.getItem('API_TOKEN'));
            request.send(formData);
            request.onreadystatechange = function () {
                if (request.readyState === 4) {
                    if (request.status === 200 && request.statusText === 'OK') {
                        if(localStorage.getItem('numPhoto') == 0)
                            AlbumServer.update(localStorage.getItem('ALBUM_ID'), {
                                cover_file: JSON.parse(request.response).data.id
                            }).then(
                                // () => //console.log(res)
                            );
                            FileLinkServer.create({
                                file_id: JSON.parse(request.response).data.id,
                                type_id: 5,
                                sheet_id: localStorage.getItem('ALBUM_ID')
                            }).then(() => {
                                // console.log(res)
                            });
                            localStorage.setItem('numPhoto', Number(localStorage.getItem('numPhoto'))+1);
                            this.message = "目前總共成功上傳" + localStorage.getItem('numPhoto') + "張圖片";
                            imageUrl.value = "";
                            imageFile.value = "";
                        } else {
                            // console.log('failed');
                        }
                }
            }
            
        }
        return {
            handleImageSelected,
            imageUrl,
            handleSubmit,
            message,
            images,
            albumName,
            albumDescription,
            albumContext,
            successID,
            numPhoto,
            editor,
            editorConfig
        };
    },
    methods: {
        createData() {
            let data = {
                name: this.albumName,
                description: this.albumDescription,
                context: this.albumContext
            };
            AlbumServer.create(data).then((response) => {
                this.message = "相簿新增成功，請上傳相片。";
                localStorage.setItem('ALBUM_ID', response.data.id);
                localStorage.setItem('numPhoto', 0);
            });
        },
        completeAlbum() {
            this.message = "相簿建立完成，請重新更新網頁前往預覽。";
            localStorage.removeItem("numPhoto");
            localStorage.removeItem("ALBUM_ID");
            this.$router.go(0);
        }
    }
};
</script>

<style scoped></style>