<template>
    <ManageLinkCreate></ManageLinkCreate>
    <!-- 編輯用 -->
    <b-modal
        id="modal-prevent-closing-4"
        ref="modal-4"
        title="編輯網址資料"
        @ok="updateLink"
    >
        <form ref="form" @submit.stop.prevent="handleSubmit">
            <b-form-group
                label="連結名稱"
                label-for="UPDATE_LINK_NAME"
                invalid-feedback="Name is required"
            >
                <b-form-input
                    id="UPDATE_LINK_NAME"
                    v-model="editinglinks.name"
                    placeholder="請填入連結名稱"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
                label="網址"
                label-for="UPDATE_LINK_URL"
                invalid-feedback="Name is required"
            >
                <b-form-input
                    id="UPDATE_LINK_URL"
                    v-model="editinglinks.url"
                    placeholder="請填入網址"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
                label="網址分類"
                label-for="UPDATE_LINK_TYPE"
                invalid-feedback=""
            >
                <b-form-input
                    id="UPDATE_LINK_TYPE"
                    v-model="editinglinks.type"
                    placeholder="請填入網址分類"
                    required
                ></b-form-input>
            </b-form-group>
        </form>
    </b-modal>

    <div>
        <table class="table w-100 table-hover">
            <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">連結名稱</th>
                    <th scope="col">網站地址</th>
                    <th scope="col">類型</th>
                    <th scope="col">操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in links" :key="item.id">
                    <th scope="row">{{ index + 1}}</th>
                    <td>{{ item.name }}</td>
                    <td>
                        <a v-if="item.download" :href="'http://localhost/ntcust_lang_backend/public/api/file/' + item.url" title="">
                            <b-button variant="warning m-2"
                                v-b-popover.hover.bottom="'file/' + item.url" 
                                title="查看檔案">
                                檔案
                            </b-button>
                        </a>
                        <a v-if="!item.download" :href="item.url" title="">
                            <b-button variant="info m-2"
                                v-b-popover.hover.bottom="item.url" 
                                title="查看連結">
                                連結
                            </b-button>
                        </a>
                    </td>
                    <td>{{ item.type }}</td>
                    <td>
                        <b-button 
                        variant="success m-1 text-end"
                        v-b-modal.modal-prevent-closing-4
                        @click="edit(index)">
                            編輯
                        </b-button>
                        <b-button variant="danger m-2">刪除</b-button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>



<script>
import { LinkServer } from '../../providers/http-server';
import ManageLinkCreate from './Manage-Link-Create.vue';

export default {
    name: 'App',
    components: {
        ManageLinkCreate
    },
    data(){
        return {
            imageFile: Object,
            // Modal
            nameState: false,
            modalShow: false,
            // data
            links: [],
            editinglinks: [],
            creatinglinks: {
                name: '',
                url: '',
                type: '',
                download: false,
                options: 1
            },
            options: [
                { text: '紀錄網址', value: 1 },
                { text: '檔案上傳', value: 2 },
            ]
        }
    },
    created(){
        LinkServer.read({group: 0}).then(
            (response) => this.links = response.data
        );
    },
    methods: {
        edit(index) {
            this.editinglinks = this.links[index];
        },
        checkFormValidity() {
            const valid = this.$refs.form.checkValidity()
            this.nameState = valid
            return valid
        },
        createLink() {
            if(this.creatinglinks.options == 2) {
                var formData = new FormData();
                formData.append("file", this.imageFile.value);
                var request = new XMLHttpRequest();
                request.open("POST", "http://localhost/ntcust_lang_backend/public/api/file/create");
                request.setRequestHeader('Authorization', 'Bearer ' + localStorage.getItem('API_TOKEN'));
                request.send(formData);
                request.onreadystatechange = function () {
                    if (request.readyState === 4) {
                        if (request.status === 200 && request.statusText === 'OK') {
                            // console.log(JSON.parse(request.response));
                        } else {
                            // console.log(JSON.parse(request.response));
                        }
                    }
                }
            }
            let data = {
                name: this.creatinglinks.name,
                url: this.creatinglinks.url,
                type: this.creatinglinks.type,
                download: this.creatinglinks.download
            };
            LinkServer.create(data).then(
                // (response) => console.log(response)
            );
            
        },
        updateLink() {
            LinkServer.update(this.editinglinks.id, this.editinglinks).then(
                // (response) => console.log(response)
            );
        },
        handleImageSelected(event) {
            // console.log(event)
            this.imageFile.value = event.target.files[0];
        }
    }
}
</script>

<style scoped></style>